.person {
	$this: &;
	margin-top: 160px;
	max-width: 505px;
	width: 100%;
	text-decoration: none;
	color: $textColor;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid #4FA3DC;
	padding: 30px;
	border-radius: 20px;
	@include laptop {
		margin-top: 90px;
		max-width: none; }
	@include sm {
		padding: 15px;
		margin-top: 60px; }
	&__name {
		font-size: $textFontSize;
		margin-bottom: 10px;
		font-family: $boldFont;
		text-align: center;
		@include laptop {
			font-size: $textFontSizeLaptop; } }
	&__photo-wrapper {
		width: 320px;
		height: 320px;
		position: relative;
		margin-top: -190px;
		@include laptop {
			height: 160px;
			width: 160px;
			margin-top: -130px; }
		@include sm {
			height: 100px;
			width: 100px;
			margin-top: -80px; } }
	&__photo {
		@include coverdiv;
		overflow: hidden;
		border-radius: 50%;
		border: 15px solid #ffffff;
		@include sm {
			border-width: 4px; }
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__icon {
		height: 88px;
		width: 88px;
		position: absolute;
		bottom: 0;
		right: 0;
		border-radius: 50%;
		background-color: $sectionBlue;
		display: flex;
		justify-content: center;
		align-items: center;
		@include laptop {
			height: 60px;
			width: 60px; }
		@include sm {
			height: 30px;
			width: 30px; }
		.icon {
			font-size: 40px;
			@include sm {
				font-size: 20px; } }
		.red & {
			background-color: $sectionRed; }
		.orange & {
			background-color: $sectionOrange; }
		.green & {
			background-color: $sectionGreen; } }
	&__desc {
		font-size: $textFontSizeXs;
		&--full {
			max-height: 320px;
			overflow: auto;
			max-width: 1100px;
			margin: 0 auto;
			p + p {
				margin-top: 20px; } } }
	&__link {
		align-self: flex-start;
		margin-top: 30px; }
	&--modal {
		width: 100%;
		max-width: none;
		@include laptop {
			max-width: none; }
		#{$this}__photo-wrapper {
			height: 378px;
			width: 378px;
			@include laptop {
				height: 200px;
				width: 200px; }
			@include sm {
				height: 140px;
				width: 140px; } }
		#{$this}__name {
			font-size: 50px;
			@include sm {
				font-size: 20px; } }
		#{$this}__icon {
			height: 103px;
			width: 103px;
			@include laptop {
				height: 80px;
				width: 80px; }
			@include sm {
				height: 40px;
				width: 40px; }
			.icon {
				font-size: 50px;
				@include sm {
					font-size: 20px; } } } } }

