body {
	font-family: $commonFont;
	background: #FFF;
	color: $textColor;
	line-height: 1.2;
	padding-bottom: 300px;
	@include sm {
		padding-bottom: 150px; } }

.out {
	overflow: hidden; }

.container {
	padding: 0 150px;
	@include laptop {
		padding: 0 40px; } }

.section-title {
	font-size: $titleFontSize;
	font-family: $boldFont;
	margin-bottom: 54px;
	@include laptop {
		font-size: $titleFontSizeLaptop; }
	@include md {
		text-align: center; }
	@include sm {
		font-size: $titleFontSizeMobile;
		margin-bottom: 15px; }
	&--center {
		text-align: center;
		margin-bottom: 80px; } }

.section-icon {
	margin-bottom: 30px;
	display: flex;
	justify-content: center; }

.text {
	line-height: 1.89;
	font-size: $textFontSizeSm;
	@include laptop {
		font-size: $textFontSizeSmLaptop; }
	@include sm {
		font-size: $textFontSizeSmMobile;
		line-height: 1.6; }
	p + p {
		margin-top: 28px;
		@include laptop {
			margin-top: 15px; } } }



