 :root {
	--app-height: 100vh; }

.hero {
	height: 100vh;
	height: var(--app-height);
	position: relative;
	z-index: 1;
	padding: 65px 150px;
	@include laptop {
		padding: 40px; }
	@include sm {
		padding: 30px; }
	&__slider {
		z-index: -2;
		@include coverdiv; }
	&__slide {
		height: 100vh;
		height: var(--app-height);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;
		z-index: 1;
		&:before {
			content: '';
			@include coverdiv;
			z-index: -1;
			pointer-events: none;
			background: linear-gradient(to bottom, #0D2C49 0%, rgba(13, 44, 73, 0.88) 2%, rgba(75, 109, 139, 0.44) 20%, rgba(82, 110, 136, 0.22) 30%, rgba(82, 110, 136, 0) 50%, rgba(28, 84, 136, 0) 70%, rgba(28, 84, 136, 0.77) 78%,#0D2C49 100%); } }
	&__content {
		padding: 150px 30px 205px;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: center;
		@include sm {
			padding: 100px 30px; } }
	&__button {
		margin-bottom: 54px;
		@include laptop {
			margin-bottom: 20px; } }
	&__text {
		font-size: $heroFontSize;
		color: #FFFFFF;
		text-align: center;
		@include laptop {
			font-size: $heroFontSizeLaptop; }
		@include sm {
			font-size: $heroFontSizeMobile; } }
	&__logo {
		max-width: 773px;
		@include laptop {
			max-width: 500px; } } }


.logo {
	width: 100%;
	img {
		width: 100%; } }



