.contacts {
	padding: 400px 0 0;
	position: relative;
	@include sm {
		padding: 160px 0 0; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 0;
		transform: translate(-35%, -43%);
		left: 50%;
		width: 1800px;
		@include sm {
			transform: translate(-35%, -37%);
			width: 450px; }
		img {
			width: 100%; } }
	&__info {
		max-width: 1200px;
		margin: 0 auto; }
	&__buttons {
		display: flex;
		justify-content: center;
		.btn + .btn {
			margin-left: 70px;
			@include md {
				margin-left: 40px; }
			@include sm {
				margin-left: 15px; } } }
	&__map {
		position: relative;
		z-index: -1;
		margin-top: -290px;
		margin-bottom: 400px;
		padding-top: 50%;
		@include laptop {
			margin-bottom: 100px;
			margin-top: -240px; }
		@include sm {
			margin-top: 0;
			margin-bottom: 10px; }
		@include xs {
			padding-top: 80%;
			margin: 0 -20px 10px; }
		img {
			@include coverdiv;
			object-fit: cover; } } }

.c-item {
	margin: 60px 0;
	display: flex;
	align-items: center;
	@include md {
		margin: 40px 0; }
	@include sm {
		align-items: flex-start;
		margin: 15px 0; }
	&__title {
		width: 50%;
		font-family: $boldFont;
		font-size: 40px;
		text-align: right;
		padding-right: 10px;
		@include laptop {
			width: 30%; }
		@include md {
			font-size: 30px; }
		@include sm {
			font-size: 10px; } }
	&__text {
		width: 50%;
		font-size: 36px;
		padding-left: 10px;
		@include laptop {
			width: 70%; }
		@include md {
			font-size: 26px; }
		@include sm {
			font-size: 9px;
			padding-left: 0; }
		a {
			color: inherit;
			&:not(.info-link) {
				text-decoration: none; } } } }
