@media only screen and (max-width: 767px) { .subscribe { padding: 180px 40px 0; } }

.subscribe__deco { z-index: -1; position: absolute; top: 100px; transform: translate(-28.5%, 0); left: 50%; width: 2000px; }

@media only screen and (max-width: 767px) { .subscribe__deco { width: 600px; top: 65px; transform: translate(-30%, 0); } }

.subscribe__deco img { width: 100%; }

.subscribe__inner { max-width: 850px; margin: 0 auto 250px; }

@media only screen and (max-width: 767px) { .subscribe__inner { margin: 0 auto 40px; padding: 0 10px; } }

.subscribe .text { max-width: 640px; margin: 0 auto; }

.subscribe__form { max-width: 1100px; margin: 0 auto; padding-right: 100px; }

@media only screen and (max-width: 767px) { .subscribe__form { margin: 0 -20px; padding-right: 0; } }

.subscribe__social { display: flex; justify-content: center; margin-top: 280px; }

@media only screen and (max-width: 767px) { .subscribe__social { margin-top: 80px; } }

.form-line { display: flex; }

.input { max-width: 505px; margin-right: 50px; border: 7px solid #0099FF; padding: 15px; border-radius: 25px; font-size: 41px; font-family: Tahoma-Bold, Arial, sans-serif; }

.input::placeholder { color: #11406A; }

@media only screen and (max-width: 1440px) { .input { font-size: 32px; } }

@media only screen and (max-width: 767px) { .input { font-size: 12px; border-width: 3px; border-radius: 10px; padding: 5px 10px; margin-right: 10px; width: 100%; max-width: none; display: block; } }

.contacts { padding: 400px 0 0; position: relative; }

@media only screen and (max-width: 767px) { .contacts { padding: 160px 0 0; } }

.contacts__deco { z-index: -1; position: absolute; top: 0; transform: translate(-35%, -43%); left: 50%; width: 1800px; }

@media only screen and (max-width: 767px) { .contacts__deco { transform: translate(-35%, -37%); width: 450px; } }

.contacts__deco img { width: 100%; }

.contacts__info { max-width: 1200px; margin: 0 auto; }

.contacts__buttons { display: flex; justify-content: center; }

.contacts__buttons .btn + .btn { margin-left: 70px; }

@media only screen and (max-width: 1023px) { .contacts__buttons .btn + .btn { margin-left: 40px; } }

@media only screen and (max-width: 767px) { .contacts__buttons .btn + .btn { margin-left: 15px; } }

.contacts__map { position: relative; z-index: -1; margin-top: -290px; margin-bottom: 400px; padding-top: 50%; }

@media only screen and (max-width: 1440px) { .contacts__map { margin-bottom: 100px; margin-top: -240px; } }

@media only screen and (max-width: 767px) { .contacts__map { margin-top: 0; margin-bottom: 10px; } }

@media only screen and (max-width: 599px) { .contacts__map { padding-top: 80%; margin: 0 -20px 10px; } }

.contacts__map img { position: absolute; top: 0; left: 0; width: 100%; height: 100%; object-fit: cover; }

.c-item { margin: 60px 0; display: flex; align-items: center; }

@media only screen and (max-width: 1023px) { .c-item { margin: 40px 0; } }

@media only screen and (max-width: 767px) { .c-item { align-items: flex-start; margin: 15px 0; } }

.c-item__title { width: 50%; font-family: Tahoma-Bold, Arial, sans-serif; font-size: 40px; text-align: right; padding-right: 10px; }

@media only screen and (max-width: 1440px) { .c-item__title { width: 30%; } }

@media only screen and (max-width: 1023px) { .c-item__title { font-size: 30px; } }

@media only screen and (max-width: 767px) { .c-item__title { font-size: 10px; } }

.c-item__text { width: 50%; font-size: 36px; padding-left: 10px; }

@media only screen and (max-width: 1440px) { .c-item__text { width: 70%; } }

@media only screen and (max-width: 1023px) { .c-item__text { font-size: 26px; } }

@media only screen and (max-width: 767px) { .c-item__text { font-size: 9px; padding-left: 0; } }

.c-item__text a { color: inherit; }

.c-item__text a:not(.info-link) { text-decoration: none; }

.modal { display: none; z-index: 10; position: fixed; top: 0; left: 0; height: 100%; width: 100%; background-color: rgba(195, 195, 195, 0.69); }

.modal__inner { max-width: 1584px; width: 100%; overflow: auto; max-height: 100%; background-color: #ffffff; border: 1px solid #707070; border-radius: 40px; padding: 90px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

@media only screen and (max-width: 1440px) { .modal__inner { padding: 30px; } }

@media only screen and (max-width: 767px) { .modal__inner { padding: 20px; } }

.modal__close { position: absolute; top: 36px; right: 36px; background-color: #F06B79; border-radius: 50%; display: flex; align-items: center; justify-content: center; font-size: 40px; height: 67px; width: 67px; }

@media only screen and (max-width: 767px) { .modal__close { width: 30px; height: 30px; font-size: 20px; top: 20px; right: 20px; } }

.modal__close .icon { display: block; }
