.desc-section {
	padding: 332px 0 110px;
	position: relative;
	z-index: 1;
	@include sm {
		padding: 150px 0 40px; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 0;
		transform: translate(-89%,-85%);
		left: 50%;
		width: 2900px;
		@include sm {
			width: 800px;
			transform: translate(-83%,-83%); }
		img {
			width: 100%; } }
	&__media {
		position: relative;
		padding-top: 52%;
		margin: 90px 0 25px;
		@include sm {
			margin: 70px -40px 25px;
			padding-top: 80%; } }
	&__slider {
		@include coverdiv;
		div {
			height: 100%; } }
	&__slide {
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center; }
	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		&--reverse {
			flex-direction: row-reverse; } }
	&__image {
		width: 48%;
		padding-top: 38%;
		background-color: #FEF3F4;
		@include md {
			display: none; } }
	&__text {
		width: 49%;
		@include md {
			width: 100%; } }
	&--first {
		z-index: 2; } }

.explore-deco {
	z-index: -1;
	position: absolute;
	top: 100px;
	transform: translate(-28.5%, 0);
	left: 50%;
	width: 2000px;
	@include sm {
		width: 590px;
		top: 25px; }
	img {
		width: 100%; } }
