.subscribe {
	padding: 400px 40px 0;
	position: relative;
	@include sm {
		padding: 180px 40px 0; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 100px;
		transform: translate(-28.5%, 0);
		left: 50%;
		width: 2000px;
		@include sm {
			width: 600px;
			top: 65px;
			transform: translate(-30%, 0); }
		img {
			width: 100%; } }
	&__inner {
		max-width: 850px;
		margin: 0 auto 250px;
		@include sm {
			margin: 0 auto 40px;
			padding: 0 10px; } }
	.text {
		max-width: 640px;
		margin: 0 auto; }
	&__form {
		max-width: 1100px;
		margin: 0 auto;
		padding-right: 100px;
		@include sm {
			margin: 0 -20px;
			padding-right: 0; } }
	&__social {
		display: flex;
		justify-content: center;
		margin-top: 280px;
		@include sm {
			margin-top: 80px; } } }

.form-line {
	display: flex; }

.input {
	max-width: 505px;
	margin-right: 50px;
	border: 7px solid $inputBorderColor;
	padding: 15px;
	border-radius: 25px;
	font-size: $btnFontSizeLg;
	font-family: $boldFont;
	@include placeholder-color($textColor);
	@include laptop {
		font-size: $btnFontSizeLgLaptop; }
	@include sm {
		font-size: $btnFontSizeMobile;
		border-width: 3px;
		border-radius: 10px;
		padding: 5px 10px;
		margin-right: 10px;
		width: 100%;
		max-width: none;
		display: block; } }
