.team {
	padding-top: 500px;
	margin-top: -100px;
	position: relative;
	z-index: 1;
	overflow: hidden;
	@include laptop {
		padding-top: 150px;
		margin-top: 0; }
	&__deco {
		z-index: -1;
		position: absolute;
		top: 0;
		transform: translate(-35%, -35%);
		left: 50%;
		width: 1900px;
		@include laptop {
			width: 1400px;
			transform: translate(-35%, -37%); }
		@include sm {
			width: 450px;
			transform: translate(-33%,-35%); }
		img {
			width: 100%; } } }

.team-section {
	margin: 115px 0;
	@include laptop {
		margin: 50px 0; }
	@include sm {
		margin: 30px 0; }
	&__title {
		font-size: $titleFontSizeMd;
		color: #ffffff;
		background-color: $sectionBlue;
		padding: 13px 10px 21px 150px;
		border-radius: 0 50px 50px 0;
		width: 78%;
		font-family: $boldFont;
		margin-bottom: 40px;
		@include laptop {
			font-size: $titleFontSizeMdLaptop;
			padding: 10px 10px 15px 40px; }
		@include sm {
			padding: 5px 10px 8px 15px;
			font-size: $titleFontSizeMdMobile;
			margin-bottom: 23px; }
		.red & {
			background-color: $sectionRed; }
		.orange & {
			background-color: $sectionOrange; }
		.green & {
			background-color: $sectionGreen; } }
	&__items {
		display: flex;
		flex-wrap: wrap;
		margin: 74px -40px 40px;
		@include laptop {
			margin: 30px -40px; }
		@include sm {
			margin: 10px -17px; } }
	&__item {
		margin: 0 35px 30px;
		@include sm {
			margin: 0 10px 30px; } } }

.team-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	cursor: pointer;
	&__title {
		text-align: center;
		width: 205px;
		margin-top: 30px;
		font-size: $textFontSizeSm;
		@include sm {
			font-size: $textFontSizeSmMobileTeam;
			width: 70px;
			margin-top: 9px; } } }

.round-item {
	border-radius: 50%;
	height: 205px;
	width: 205px;
	background-color: $sectionBlue;
	display: flex;
	align-items: center;
	justify-content: center;
	@include laptop {
		height: 140px;
		width: 140px; }
	@include sm {
		height: 65px;
		width: 65px; }
	.icon {
		font-size: 125px;
		display: block;
		@include laptop {
			font-size: 80px; }
		@include sm {
			font-size: 30px; } }
	.red & {
		background-color: $sectionRed; }
	.orange & {
		background-color: $sectionOrange; }
	.green & {
		background-color: $sectionGreen; }
	.rose & {
		background-color: $sectionrose; } }

.persons {
	display: flex;
	margin: 0 -25px;
	flex-wrap: wrap;
	@include sm {
		margin: 0; }
	&__item {
		padding: 25px;
		&:not(:last-child) {
			@include sm {
				margin-bottom: 30px; } }
		@include laptop {
			width: 50%; }
		@include sm {
			width: 100%;
			padding: 0; } } }
